* { 
    margin: 0;
    padding: 0;    
    box-sizing: border-box;
   
}

#root {
    display: flex;
    align-items: center;
    flex-direction: column;

}

.App {
    width: 800px;

}

.my-input {
    width: 100%;
    padding: 5px 15px;
    margin: 15px 0 ;
    border: 2px solid rgb(78, 0, 94);
    border-radius: 5px;
}


.info-box {
    border-radius: 5px;
    border: 2px solid rgb(78, 0, 94) ;
}


.info-field  {
    display: flex;
    margin: 15px;
    box-sizing: border-box;
    padding: 5px;
    /* border: 2px   solid rgb(78, 0, 94) ; */

    

}